<template>
  <div class="historypage tapStyle secondary-page-mode">
     <div class="secondary-toolbar">
          <span>时间选择 : </span>&nbsp;
          <div class="layoutBox" style="width:242px">
            <el-date-picker v-model="dateRange" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
          </div>&emsp;&emsp;
          <span>停站原因 : </span>&nbsp;
          <div class="layoutBox" style="width:120px">
            <el-select v-model="value" placeholder="请选择" @change="selectChange">
                  <el-option
                    v-for="item in optionData"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
            </el-select>
          </div>&emsp;&emsp;
          
          <el-button><i class="iconfont icon-sousuo"></i>查询</el-button>
          <el-button @click="applyOutage = true"><i class="iconfont icon-dingwei"></i>停站申请</el-button>
          <el-button type='delet'><i class="iconfont icon-daochu"></i>导出</el-button>
     </div>
     <div class="currency-mode-wrap">
          <yf-tabs type="border-card">
            <yf-tab-pane label="当前断面">
                    <div class="tablebox">
                        <el-table
                          :data="tableData"
                          border
                          height="calc(100% - 37px)"
                         >
                          <el-table-column
                            type="index"
                            label="序号"
                            :index="indexMethod"
                            width="50">
                          </el-table-column>
                          <el-table-column
                            prop="name"
                            label="断面名称">
                          </el-table-column>
                           <el-table-column
                            prop="name"
                            label="开始时间">
                          </el-table-column>
                           <el-table-column
                            prop="name"
                            label="结束时间">
                          </el-table-column>
                           <el-table-column
                            prop="name"
                            label="停站原因">
                          </el-table-column>
                           <el-table-column
                            prop="name"
                            label="是否生效">
                          </el-table-column>
                           <el-table-column
                            prop="name"
                            label="操作人">
                          </el-table-column>
                           <el-table-column
                            prop="name"
                            label="备注">
                          </el-table-column>
                           <el-table-column
                            prop="name"
                            label="操作">
                            <template>
                              <div>
                                  <el-button type="secondBtn" @click="toExamine = true">审核</el-button>
                              </div>
                            </template>
                          </el-table-column>
                        </el-table>
                        <div class="table-pagination-wrap">
                          <el-pagination
                            @size-change="paginationSizeChange"
                            @current-change="paginationChange"
                            :current-page.sync="currentPage"
                            :page-size="pageSize"
                            popper-class="select-pagination-option"
                            background
                            :small="true"
                            :page-sizes="pageList"
                            layout="total, sizes, slot,->,prev, pager, next, jumper"
                            :total="total">
                            <template v-slot:default>
                              <span class="el-pagination__current">当前第{{ currentPage }}/{{totalPageNumber}}页</span>
                            </template>
                          </el-pagination>
                        </div>
                    </div>
            </yf-tab-pane>
            <yf-tab-pane label="所有断面">

              数据图表
            </yf-tab-pane>
          </yf-tabs>
     </div>

      <!-- 审核弹窗 -->
     <el-dialog
        width="540px"
        title="审核"
        :visible.sync="toExamine" >
        <div class="to-examine-wrap">
          <h4> 停运信息</h4>
          <div class="row-wrap">
            <table class="examine-table">
              <tr>
                <th>表单号</th>
                <td>YX-TYSB+GJSZHN001</td>
              </tr>
              <tr>
                <th>站点</th>
                <td>橘子洲</td>
              </tr>
              <tr>
                <th>停运开始时间</th>
                <td>2019-03-02 08:00:00</td>
              </tr>
              <tr>
                <th>停运结束时间</th>
                <td>2019-03-02 08:00:00</td>
              </tr>
              <tr>
                <th>停运原因</th>
                <td>山里降雨天气导致断电</td>
              </tr>
              <tr>
                <th>备注</th>
                <td>停运时间为预估，具体未定</td>
              </tr>
              <tr>
                <th>附件</th>
                <td><span class="field-span">橘子洲20190301停运申报单.pdf</span></td>
              </tr>
            </table>
          </div>
          <h4> 审批</h4>
          <div class="row-wrap" style="padding-left: 20px;">
            <el-radio v-model="approval" label="1">同意</el-radio>
            <el-radio v-model="approval" label="2">不同意</el-radio>
          </div>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button type="noticonsecondBtn" @click="toExamine = false">取 消</el-button>
          <el-button @click="">确 定</el-button>
        </div>
     </el-dialog>

    <!-- 申请停运 -->
    <el-dialog
      width="540px"
      title="申请停运"
      :visible.sync="applyOutage" >
      <div class="apply-outage-wrap">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="apply-outage-form">
          <el-form-item label="表单号" prop="number">
            <el-input v-model="ruleForm.number" placeholder="请输入表单号"></el-input>
          </el-form-item>
          <el-form-item label="站点" prop="siteName">
            <el-select v-model="ruleForm.siteName" placeholder="请选择站点">
              <el-option
                v-for="item in siteOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="停运开始时间" prop="startTime">
            <el-date-picker
              v-model="ruleForm.startTime"
              type="date"
              placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="停运结束时间" prop="endTime">
            <el-date-picker
              v-model="ruleForm.endTime"
              type="date"
              placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="停运原因" prop="reason" style="margin-bottom: 20px;">
            <el-input v-model="ruleForm.reason" placeholder="请输入停运原因"></el-input>
          </el-form-item>
          <el-form-item label="备注" prop="remarks" >
            <el-input v-model="ruleForm.remarks" type="textarea" :rows="2" placeholder="请输入备注"></el-input>
          </el-form-item>
          <el-form-item label="附件上传" prop="enclosure">
            <el-upload
              class="upload-demo"
              ref="upload"
              :file-list="enclosure"
              :auto-upload="false">
              <el-button type="noticonsecondBtn"><i class="iconfont icon-shangchuan1"></i> 选择文件</el-button>
            </el-upload>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="noticonsecondBtn" @click="applyOutage = false">取 消</el-button>
        <el-button @click="">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      dateRange: "",
      value: "",
      optionData: [
        { value: 1, label: "水温", standard: "", isShow: true },
        { value: 2, label: "PH", standard: "6~9", isShow: true },
        { value: 3, label: "溶解氧", standard: "≥5", isShow: true },
        { value: 4, label: "电导率", standard: "", isShow: true },
        { value: 5, label: "浊度", standard: "", isShow: true },
        { value: 6, label: "高锰酸盐", standard: "6", isShow: true },
        { value: 7, label: "氨氮", standard: "1", isShow: true },
        { value: 8, label: "总磷", standard: "0.2", isShow: true },
        { value: 9, label: "总氮", standard: "", isShow: true }
      ],
      selectCheck: [], //选中的监测项目
      tableData: [{}],
      currentPage: 1, //分页组件当前页
      total: 200, //分页总条数
      pageSize: 10, //分页每页多少条数据
      pageList: [10, 15, 20], //设置每页多少条
      toExamine: false,
      approval: '1',
      applyOutage: false,
      ruleForm:{
        number:'',
        siteName:'',
        startTime:'',
        endTime:'',
        reason:'',
        enclosure:[]
      },
      rules:{
        number: [
          { required: true, message: '请输入表单号', trigger: 'blur' },
        ],
        reason: [
          {required: true, message: '请输入停运原因', trigger: 'blur' },
        ]
      },
      siteOptions:[
        {
          value: '1',
          label: '站点1'
        },{
          value: '2',
          label: '站点2'
        },{
          value: '3',
          label: '站点3'
        },
      ]
    };
  },
  created() {
    this.$store.commit("setShowlefttree", false);
  },
  methods: {
    selectChange(arr) {
      this.selectCheck = arr;
    },
    indexMethod(index) {
      return index + 1;
    },
    paginationSizeChange() {},
    paginationChange() {}
  },
  computed: {
    stationInfo: {
      get: function() {
        return this.$store.state.stationInfo;
      }
    },
    totalPageNumber() {
      //计算总页数
      let number = 0;
      number = Math.ceil(this.total / this.pageSize);
      return number;
    }
  },
  watch: {
    stationInfo: function(newValue, oldValue) {
      // if(newValue[0].label != oldValue[0].label){
      //    console.log(newValue[0].label)
      // }
      console.log(newValue[0].label);
    }
  }
};
</script>

<style scoped lang="less">
.historypage {
  height: 100%;
}
.to-examine-wrap{
  h4{
    padding-left: 18px;
    background: url("../../assets/images/list-icon.png") no-repeat left center;
    font-size: 13px;
    font-weight: 100;
    line-height: 34px;
  }
  .row-wrap{
    padding-bottom: 4px;
  }
  .examine-table{
    font-size: 13px;
    border-collapse: collapse;
    width: 100%;
    th{
      text-align: right;
    }
    th,td{
      font-weight: 100;
      padding: 5px 12px;
      border: 1px solid #D0DDED;
      color: #1a1a1a;
    }
    .field-span{
      cursor: pointer;
      color: #0b61ff;
      opacity: 0.8;
    }
    .field-span:hover{
      opacity: 1;
    }
  }
}
  .dialog-footer{
    button{
      vertical-align: middle;
    }
  }
  .el-form-item{
    margin-bottom: 12px;
  }
</style>



